@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&family=Roboto&family=Work+Sans:ital,wght@0,400;0,500;0,700;1,400&display=swap");
.terms-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: "Poppins", sans-serif;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

h2 {
  font-size: 17px;
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 500;
}

p {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 20px;
  font-weight: 400;
}

.highlight {
  font-weight: 500;
  font-size: 16px;
}
