body {
  background-color: white;
}

.header {
  overflow: hidden;
  padding: 15px 100px;
  background-color: white;
}

/* Style the header links */
.header a {
  float: left;
  color: black;
  text-align: center;
  margin-top: 4px;
  padding: 10px;
  text-decoration: none;
  font-size: 15px;
  line-height: 15px;
  border-radius: 4px;
}

.main-image {
  height: 80px;
  width: 250px;
  object-fit: contain;
}

/* Change the background color on mouse-over */
.header a:hover {
  background-color: #ddd;
  color: black;
  cursor: pointer;
}

/* Style the active/current link*/
.header a.activeh {
  background-color: #e23744;
  color: white;
}

/* Float the link section to the right */
.header-right {
  float: right;
}

/* MainContent.css */

.app {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  gap: 10rem;
  font-family: "poppins";
}

/* Add more styles for other elements as needed */

/* Add media queries for responsiveness - when the screen is 560px wide or less, stack the links on top of each other */
@media screen and (max-width: 560px) {
  .header {
    overflow: hidden;
    background-color: #ffffff;
    padding: 20px 10px;
  }
  .main-image {
    height: 30px;
    width: 130px;
    margin-top: 7px;
    object-fit: contain;
  }
  .app {
    flex-direction: column;
    gap: 0;
  }
}
